<template>
    <div id="wrap">
        <AppHeader />
        <SideBar />
        <div class="pointContainer">
            <div class="container5">
                <!--   -->
                <div class="useablePointContainer">
                    <div class="upcTop">
                        <h2> </h2>
                        <div>
                            <p>   : <span>{{ userVo.uPoint.toLocaleString() }}P</span></p>
                        </div>
                    </div>
                </div>

                <!--   -->
                <div class="pointSearchContainer">
                    <!--   -->
                    <div class="pscLeftContainer">
                        <!--    -->
                        <div class="searchByTermBtns">
                            <button :class="{ active: activeButtonIndex === 0 }"
                                @click="setActiveButton(0)">1</button>
                            <button :class="{ active: activeButtonIndex === 1 }"
                                @click="setActiveButton(1)">3</button>
                            <button :class="{ active: activeButtonIndex === 2 }"
                                @click="setActiveButton(2)">6</button>
                            <button :class="{ active: activeButtonIndex === 3 }"
                                @click="setActiveButton(3)">12</button>
                        </div>

                        <!--   -->
                        <div class="searchByTermInputs">
                            <input type="date">~<input type="date">
                        </div>
                    </div>
                    <!--   -->
                    <button class="pscRightContainer">
                        
                    </button>
                </div>

                <!--   -->
                <div class="pointContentsContainer">
                    <table border="1">
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th>/</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-bind:key="i" v-for="(userVo, i) in userList">
                                <td>{{ formatDate(userVo.pDate) }}</td>
                                <td v-if="userVo.pDiv"> {{ userVo.usePoint }} P  </td>
                                <td v-else> {{ userVo.usePoint }} P  </td>
                                <td v-if="userVo.pDiv"></td>
                                <td v-else></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <TopButton />
        <AppFooter />
    </div>
</template>

<script>
import AppHeader from "@/components/AppHeader.vue"
import AppFooter from "@/components/AppFooter.vue"
import TopButton from "@/components/TopButton.vue"
import SideBar from "@/components/SideBar.vue"
import '@/assets/css/mypage/point.css'
import axios from "axios"

export default {
    name: "PointView",
    components: {
        AppHeader,
        AppFooter,
        SideBar,
        TopButton
    },
    data() {
        return {
            userVo: {
                uNo: this.$store.state.authUser.uNo,
                rsNo: 0,
                pDate: "",
                uPoint: 0,
                bNo: 0,
                rtFinish: false,
                usePoint: 0,
            },
            userList: [],
            activeButtonIndex: null, //    
        };
    },

    computed: {
        //   
        formatDate() {
            return (dateString) => {
                const date = new Date(dateString);
                const year = date.getFullYear();
                const month = String(date.getMonth() + 1).padStart(2, '0');
                const day = String(date.getDate()).padStart(2, '0');
                return `${year}-${month}-${day}`;
            };
        },
    },
    methods: {
        setActiveButton(index) {
            if (this.activeButtonIndex === index) {
                this.activeButtonIndex = null; //      
            } else {
                this.activeButtonIndex = index;
            }
        },

        //  
        getPoint() {
            console.log("  ");
            console.log(this.userVo.uNo);
            axios({
                method: 'get',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/mypage/getpoint`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { uNo: this.userVo.uNo },
                responseType: 'json' //
            }).then(response => {
                console.log("  ");
                console.log(response.data.apiData); //
                this.userVo = response.data.apiData;
            }).catch(error => {
                console.log(error);
            });
        },

        // 
        getPointList() {
            console.log(" ");
            axios({
                method: 'get',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/mypage/getpointlist`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { uNo: this.userVo.uNo },
                responseType: 'json' //
            }).then(response => {
                console.log("  ");
                console.log(response.data.apiData); //
                this.userList = response.data.apiData;
            }).catch(error => {
                console.log(error);
            });
        }
    },
    created() {
        this.getPoint();
        this.getPointList();
    }
};
</script>

<style scoped>
.pointContainer .searchByTermBtns button {
    background-color: #e0e0e0;
    /*   */
    border: none;
    padding: 10px 20px;
    margin: 5px;
    cursor: pointer;
    color: black;
}

.pointContainer .searchByTermBtns button.active {
    background-color: green;
    /*    */
    color: white;
}
</style>